$font-style: normal;
$font-variant: normal;
$font-weight: normal;
$font-size: 1rem;
$line-height: 1.5;
$open: 'Open Sans', sans-serif;
$source: 'Source Code Pro', monospace;
$black: rgb(60,60,60);
$almost-black: rgb(120,120,120);
$gray: rgb(245,245,245);
$gold: rgb(218,178,82);
$blue: rgb(82,177,226);

html {
  box-sizing: border-box;
  color: $black;
  font-family: $source;
}

body {
  background: $gray;
  font-size: $font-size;
  line-height: $line-height;
  padding: 0px;
  margin: 0;
}

img {
	display: block;
	border: 0;
	max-width: 100%;
	height: auto;
}

.site {
  margin: 2rem auto 0;
}

/* TYPOGRAPGHY */

h1,h2,h3,h4,h5 {
  font-family: $open;
  margin: 0;
}

h1 a {
  color: $black;
  display: inline-block;
  font-size: 1.75rem;
  font-weight: 800;
  letter-spacing: .08ch;
  text-decoration: none;
  text-transform: uppercase;
  transition: .15s;
}

h1 a:hover {
  opacity: .15;
  transition: .2s;
}

nav {
  a {
    align-items: center;
    border-radius: 4px;
    color: $black;
    display: inline-flex;
    font-weight: 600;
    height: 36px;
    justify-content: center;
    margin-left: 4px;
    min-width: 60px;
    padding: 0 10px;
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    white-space: pre-wrap;
    z-index: 0;
  }
  a::before {
    content: ' ';
    display: block;
    opacity: 0;
    position: absolute;
    transition-duration: .2s;
    transition-timing-function: cubic-bezier(0.4,0.0,0.2,1);
    z-index: -1;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    background: $black;
    border-radius: 4px;
    transform: scale(0);
    transition-property: transform,opacity;
  }
  a:hover::before {
    opacity: .06;
    transform: scale(1);
    transition-duration: .2s;
  }
  a:active::before,a:focus::before {
    opacity: .15;
    transform: scale(1);
    transition-duration: .2s;
  }
}

/* HEADER & NAV STYLES */

header {
  align-items: baseline;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

nav {

  ul {
    list-style: none;
    padding: 0 .5rem;

    li{
      display: inline-block;
    }

    li + li {
      padding-left: 1rem;
    }
  }

  a {

  }
}
/* MAIN STYLES */

.about {
  display: flex;
  flex-direction: column;
  text-align: center;

  h2 {
    font-size: 1rem;
    letter-spacing: .08ch;
    text-transform: uppercase;
  }
}

/*.site {
  grid-column: ;
